import uniCard from "@/components/uni-card/uni-card.vue"
import uniList from '@/components/uni-list/uni-list.vue'
import uniListItem from '@/components/uni-list-item/uni-list-item.vue'
import uniRate from '@/components/uni-rate/uni-rate.vue'
import uniTag from '@/components/uni-tag/uni-tag.vue'
import http from '../../store/http.js'

export default {
	components: {
		uniList,
		uniListItem,
		uniRate,
		uniTag,
		uniCard
	},
	data() {
		return {
			result:{},
			visible:false,
			test:'1111',
			extraIcon1: {
				color: '#007aff',
				size: '22',
				type: 'info-filled'
			},
			extraIcon2: {
				color: '#4cd964',
				size: '22',
				type: 'spinner'
			},
			MR:null,
			inspections:[],
			prescriptions:[],
		}
	},
	onLoad(options) {
		this.MR=JSON.parse(options.MR)
		this.getItems()
	},
	methods: {
		returnI(){
			this.visible=false
		},getItems(){
			let opts={
                 	url: '/medical_record/getDrugNonDrugAndResult/'+this.MR.id,
                    method: 'GET'
             };
             
	    	http.httpTokenRequest(opts, null).then(res => {
				let result=res.data.data
				console.log(result)
				this.prescriptions=result[1]
				this.inspections=result[0]

            },error => {
				uni.showToast({
					title: '网络错误，请稍后重试',
					duration: 2000,
					icon:'none'
				});  
			})			
		},checkResult(item){
			this.result.text=item.text
			this.result.img=item.pic
			this.result.name=item.nonDrugName
			uni.navigateTo({
				url:'inspectionResult?r='+JSON.stringify(this.result)
			})
		}
	}
}
